import React, { useState, useRef, useCallback } from 'react';
import './predicas.css';

function Predicas() {
    const [visibleCards, setVisibleCards] = useState(3);
    const [activeAudio, setActiveAudio] = useState(null);
    const audioRefs = useRef({});

    const predicas = [
        {
            id: 1,
            titulo: "Isaias 49",
            fecha: "23/04/2025",
            predicador: "Jessica Lopez",
            duracion: "21min",
            audioSrc: process.env.PUBLIC_URL + "/predica/isaias49-jessi.mp3"
        },
        {
            id: 2,
            titulo: "Santa Cena",
            fecha: "20/04/2025",
            predicador: "Gustavo Farias",
            duracion: "20min",
            audioSrc: process.env.PUBLIC_URL + "/predica/isaias-pastor.mp3"
        },
        {
            id: 3,
            titulo: "Libro de Isaias",
            fecha: "17/04/2025",
            predicador: "Luis Hernandez",
            duracion: "30min",
            audioSrc: process.env.PUBLIC_URL + "/predica/isaias-luis.mp3"
        },
    ];

    const pauseActiveAudio = () => {
        if (activeAudio && audioRefs.current[activeAudio]) {
            const current = audioRefs.current[activeAudio];
            current.pause();
            current.currentTime = 0;
        }
    };

    const toggleAudio = useCallback((audioId) => {
        const selectedAudio = audioRefs.current[audioId];
        if (!selectedAudio) return;

        if (activeAudio === audioId) {
            selectedAudio.pause();
            setActiveAudio(null);
        } else {
            pauseActiveAudio();
            selectedAudio.play().catch(error => {
                console.error("Error al reproducir audio:", error);
                setActiveAudio(null);
            });
            setActiveAudio(audioId);
        }
    }, [activeAudio]);

    return (
        <section className="predicas-section" id="predicas">
            <div className="container py-3 shadow-lg">
                <div className="section-header text-center mb-1">
                    <h2 className="section-title mb-3">Prédicas Recientes</h2>
                    <p className="section-subtitle">Escucha las enseñanzas más recientes de nuestra congregación</p>
                </div>

                <div className="row">
                    {predicas.slice(0, visibleCards).map((predica) => (
                        <div className="col-lg-4 col-md-6 mb-2" key={predica.id}>
                            <div className={`card predica-card h-100 ${activeAudio === predica.id ? 'active' : ''}`}>
                                <div className="card-header pt-3">
                                    <h5>{predica.titulo}</h5>
                                    <div className="waveform">
                                        <div className="wave"></div>
                                        <div className="wave"></div>
                                        <div className="wave"></div>
                                    </div>
                                </div>
                                <div className="card-body">
                                    <div className="predica-meta mb-3">
                                        <p><i className="bi bi-calendar-event me-2"></i> <strong>Fecha:</strong> {predica.fecha}</p>
                                        <p><i className="bi bi-person-fill me-2"></i> <strong>Predicador:</strong> {predica.predicador}</p>
                                        <p><i className="bi bi-alarm me-2"></i> <strong>Duración:</strong> {predica.duracion}</p>
                                    </div>

                                    <div className={`audio-player ${activeAudio === predica.id ? 'expanded mb-2' : 'd-none'}`}>
                                        <audio
                                            ref={el => audioRefs.current[predica.id] = el}
                                            src={predica.audioSrc}
                                            preload="none"
                                            controls
                                            onEnded={() => setActiveAudio(null)}
                                            onError={(e) => console.error("Error de audio:", e)}
                                            className="w-100"
                                        />
                                    </div>

                                    <button
                                        className="btn-play mt-3 w-100"
                                        onClick={() => toggleAudio(predica.id)}
                                        aria-label={activeAudio === predica.id ? 'Pausar audio' : 'Reproducir audio'}
                                    >
                                        <i className={`bi ${activeAudio === predica.id ? 'bi-pause-fill' : 'bi-play-fill'}`}></i>
                                        {activeAudio === predica.id ? ' Pausar' : ' Escuchar'}
                                    </button>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>

                <div className="text-center mt-3">
                    <a
                        href="https://youtube.com/@ccpv600?si=JZLxg-sJgSS6RNE2"
                        className="btn btn-predicas"
                        target="_blank"
                        rel="noopener noreferrer"
                    >
                        <i className="bi bi-youtube me-2"></i>
                        Escuchar más en Youtube
                    </a>
                </div>
            </div>
        </section>
    );
}

export default Predicas;
